function Home() {
    return (
        <div className="home">
            <div className="home-text">
            <div>
                <div>
                    {/* main heading */}
                    <h1>Your PURRfect pet</h1>
                    {/* Introduction */}
                    <h2>A home without a cat is only a house.</h2>
                </div>
                <ul>
                    <li>
                        {" "}
                        <p>
                            Introducing your cat to their new home should be
                            done slowly and carefully - don't rush it!
                        </p>
                    </li>
                    <li>
                        {" "}
                        <p>
                            Getting your cat microchipped is important. If they
                            go missing, your details can be quickly traced
                        </p>
                    </li>
                    <li>
                        <p>
                            Having your cat neutered reduces the risk of your
                            cat fighting and sustaining injury, contracting
                            diseases or becoming lost
                        </p>
                    </li>
                    <li>
                        {" "}
                        <p>
                            Provide appropriate health care for your cat,
                            including vaccination, flea and worming treatment
                            and regular vet check-ups
                        </p>
                    </li>
                    <li>
                        {" "}
                        <p>
                            If you're keen on buying a cat, make sure it is from
                            a reputable breeder or seller like cats4sale
                        </p>
                    </li>
                </ul>
            </div>

            <div>
                {/* Welfare advice */}
                <h2>The five welfare needs</h2>
            </div>

            <div>
                <ol>
                    <li>to have a suitable environment</li>
                    <li>to have a suitable diet</li>
                    <li>to be able to exhibit normal behaviour patterns</li>
                    <li>to have the opportunity to be alone</li>
                    <li>
                        to be protected from pain, suffering, injury and disease
                    </li>
                </ol>
            </div>

            <div>
                {/* Safe introduction to new home advise */}
                <h2>Make your home cat safe</h2>
                <p className="home-cat-safe">
                    Many people have no idea that certain normal household items
                    can be deathly to cats. Bouquets containing lilies are a
                    no-no for cat owners. The pollen is highly toxic and at our
                    veterinary hospitals we do treat cats who have been poisoned
                    by licking the pollen from their fur. Make sure to keep
                    cleaning products and medicines stored safely away. Cats
                    love to climb and walk across worktops and kitchen cupboards
                    and may knock bottles or tubs off the shelves, resulting in
                    spillages which they may then eat or lick. Antifreeze is
                    also toxic to cats and can cause vomiting, seizures and
                    breathing difficulties, among other health problems. Keep
                    your cat inside when cars are being de-iced, and call your
                    vet immediately if you think your pet has swallowed any.
                </p>
            </div>

            {/* footer */}
            <footer className="footer">
                <div>
                    <p>Copyright Cats4$ale &copy; 2021</p>
                </div>
            </footer>
            </div>
        </div>
    );
}

export default Home;
